export function IconCloudDatabases() {
  return (
    <div className="relative flex size-full max-w-lg items-center justify-center overflow-hidden rounded-lg  bg-background">
      <IconCloud
        icons={[
          // 1
          <Icons.oracle key="oracle" />,
          <Icons.mongodb key="mongodb" />,
          <Icons.postgresql key="postgresql" />,
          <Icons.virtualbox key="virtualbox" />,

          // 2
          <Icons.oracle key="oracle" />,
          <Icons.mongodb key="mongodb" />,
          <Icons.virtualbox key="virtualbox" />,
          <Icons.postgresql key="postgresql" />,

          // 3
          <Icons.oracle key="oracle" />,
          <Icons.postgresql key="postgresql" />,
          <Icons.mongodb key="mongodb" />,
          <Icons.virtualbox key="virtualbox" />,

          // 4
          <Icons.oracle key="oracle" />,
          <Icons.postgresql key="postgresql" />,
          <Icons.virtualbox key="virtualbox" />,
          <Icons.mongodb key="mongodb" />,

          // 5
          <Icons.oracle key="oracle" />,
          <Icons.virtualbox key="virtualbox" />,
          <Icons.mongodb key="mongodb" />,
          <Icons.postgresql key="postgresql" />,

          // 6
          <Icons.oracle key="oracle" />,
          <Icons.virtualbox key="virtualbox" />,
          <Icons.postgresql key="postgresql" />,
          <Icons.mongodb key="mongodb" />,

          // 7
          <Icons.mongodb key="mongodb" />,
          <Icons.oracle key="oracle" />,
          <Icons.postgresql key="postgresql" />,
          <Icons.virtualbox key="virtualbox" />,

          // 8
          <Icons.mongodb key="mongodb" />,
          <Icons.oracle key="oracle" />,
          <Icons.virtualbox key="virtualbox" />,
          <Icons.postgresql key="postgresql" />,
        ]}
      />
    </div>
  );
}
